
#header-nav {
  color: #fff;
  background-color: #0095DA;
  position: fixed;
  z-index: 100;
  left: 0;
  width: 100%;
  height: 2rem;
}
.header-back {
  position: absolute;
  left: 0;
  top: 0;
  width: 2rem;
  height: 2rem;
  line-height: 2rem;
  text-align: center;
}
.header-back:after {
    content: " ";
    display: inline-block;
    height: 0.4rem;
    width: 0.4rem;
    border-width: 2px 2px 0 0;
    border-color: #fff;
    border-style: solid;
    -webkit-transform: matrix(-0.71, -0.71, 0.71, -0.71, 0, 0);
            transform: matrix(-0.71, -0.71, 0.71, -0.71, 0, 0);
    position: relative;
}
.header-right {
  position: absolute;
  right: 0;
  top: 0;
  width: 2rem;
  height: 2rem;
  line-height: 2rem;
  text-align: center;
}
.header-title {
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 0.8rem;
  color: #fff;
  text-align: center;
}



