* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
body {
  margin: 0;
}
.xa-page {
  position: fixed;
  font-size: .6rem;
  height: 100%;
  width: 100%;
}
.xa-content {
  width: 100%;
  height: 100%;
  background-color: #f8f8f8;
}
.xa-content.with-header {
    padding-top: 2rem;
}
.xa-content.with-footer {
    padding-bottom: 2rem;
}
.xa-btn {
  position: relative;
  display: block;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  box-sizing: border-box;
  font-size: 0.6rem;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  line-height: 1.6rem;
  border-radius: 0.2rem;
  overflow: hidden;
}
.xa-btn:after {
    content: " ";
    width: 200%;
    height: 200%;
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid rgba(0, 0, 0, 0.2);
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    box-sizing: border-box;
    border-radius: 0.4rem;
}
.xa-btn:active {
    color: rgba(255, 255, 255, 0.6);
}
.xa-btn_warn {
    background-color: #E64340;
}
.xa-btn_warn:active {
      background-color: #CE3C39;
}
.xa-btn_primary {
    background-color: #0095DA;
}
.xa-btn_primary:active {
      background-color: #004581;
}
.xa-btn_default {
    background-color: #F8F8F8;
    color: #000000;
}
.xa-btn_default:active {
      background-color: #DEDEDE;
      color: rgba(0, 0, 0, 0.6);
}

/*gap between btn*/
.xa-btn + .xa-btn {
  margin-top: 0.6rem;
}
button.xa-btn, input.xa-btn {
  width: 100%;
  border-width: 0;
  outline: 0;
}
.xa-lists {
  margin-top: 0.7rem;
  background-color: #FFFFFF;
  line-height: 1.4;
  font-size: 0.4rem;
  overflow: hidden;
  position: relative;
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
}
a.xa-list {
  text-decoration: none;
  color: inherit;
}
a.xa-list:active {
    background-color: #C7E0F4;
}
.xa-list {
  padding: 0.5rem 0.7rem;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: 1px solid #e5e5e5;
}
.xa-list:last-child {
    border-bottom: none;
}
.xa-list__bd {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 0.6rem;
}
.xa-list__ft {
  text-align: right;
  color: #999999;
}
.xa-list__access:active {
  background-color: #ECECEC;
}
.xa-list__access:focus {
  outline: none;
}
.xa-list__access .xa-list__ft {
  padding-right: 13px;
  position: relative;
}
.xa-list__access .xa-list__ft:after {
    content: " ";
    display: inline-block;
    height: 6px;
    width: 6px;
    border-width: 2px 2px 0 0;
    border-color: #C8C8CD;
    border-style: solid;
    -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
            transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
    position: relative;
    position: absolute;
    top: 50%;
    margin-top: -4px;
    right: 2px;
}
.xa-nav {
  height: 1.2rem;
  width: 12rem;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.xa-nav a {
    display: block;
    text-align: center;
    padding: .2rem .4rem;
    color: #787878;
}
.xa-nav a.active {
      border-bottom: 2px solid #0095DA;
      color: #0095DA;
}
.xa-nav-ver {
  background-color: #f5f5f5;
  width: 3.5rem;
  height: calc(100% - 6rem - 2rem);
  top: 0;
  left: 0;
  margin-top: 8rem;
  position: fixed;
}
.xa-nav-ver ul {
    padding: 0;
    margin: 0;
}
.xa-nav-ver li {
    list-style: none;
    font-size: 0.6rem;
    padding: .6rem .4rem;
    border-bottom: 1px solid #ECECEC;
    border-left: 0.2rem solid;
    border-left-color: transparent;
    text-align: center;
}
.xa-nav-ver li.router-link-active {
      border-left: 0.2rem solid #0095DA;
      background-color: #fff;
}
.xa-mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}
.xa-dialog {
  position: fixed;
  z-index: 5000;
  width: 80%;
  max-width: 6rem;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: #FFFFFF;
  text-align: center;
  border-radius: 3px;
  overflow: hidden;
}
.xa-loading {
  position: absolute;
  z-index: 1000;
  width: 100%;
  height: 100%;
  opacity: 1;
}
.xa-loading__bd {
    position: fixed;
    z-index: 5000;
    width: 4rem;
    height: 4rem;
    top: 50%;
    left: 50%;
    margin-left: -2rem;
    margin-top: -4rem;
    text-align: center;
    color: #C7E0F4;
}
.xa-loading__icon {
    display: inline-block;
    -webkit-animation: xaLoading 1s steps(12, end) infinite;
            animation: xaLoading 1s steps(12, end) infinite;
}
.xa-loading__text {
    font-size: 0.7rem;
}
@keyframes xaLoading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
@-webkit-keyframes xaLoading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: fixed;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 100%;
  width: 100%;
  position: fixed;
}
.slide-left-enter-active, .slide-right-enter-active {
  transition: all 0.3s;
}
.slide-left-leave-active, .slide-right-leave-active {
  transition: all 0.3s;
}
.slide-left-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide-right-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.slide-left-leave-to {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.slide-right-leave-to {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}



